.xWTM_G_exhibition {
  white-space: pre-line;
  max-height: 35rem;
  transition: all .25s;
  overflow: hidden;
}

.xWTM_G_exhibition:hover {
  max-height: 100rem;
}

.IvaCSa_ground {
  background: linear-gradient(90deg, #5f1c84 0%, #221f66 100%);
  margin-bottom: 20px;
  padding-bottom: 30px;
}

.IvaCSa_main-video {
  height: 90vh;
}

.IvaCSa_buttonsTray {
  text-align: center;
  background: #d298db;
  border-radius: 20px;
  height: 33px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
}

@media (width >= 800px) {
  .IvaCSa_buttonsTray {
    max-width: 720px;
  }
}

.IvaCSa_buttons {
  color: #a324f2;
  background: #fff;
  border-radius: 20px;
  margin-left: 5px;
  margin-right: 5px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: .8em;
  font-weight: bold;
}

.IvaCSa_logos {
  float: none;
  margin-top: 30px;
  padding-left: 20px;
  padding-right: 20px;
  display: block;
}

.IvaCSa_frame {
  text-align: center;
  border: 2px solid #efdb94;
  border-radius: 20px;
  height: 100%;
  padding-bottom: 1rem;
  padding-left: 10px;
  padding-right: 10px;
  display: block;
  position: relative;
}

.IvaCSa_frame .IvaCSa_tag {
  color: #000;
  backdrop-filter: blur(4px);
  background: #efdb94;
  border-radius: 20px;
  height: 23px;
  margin: -12px auto 10px;
}

.IvaCSa_frame.IvaCSa_VIP {
  border-color: #fc6100;
}

.IvaCSa_frame.IvaCSa_VIP .IvaCSa_tag {
  background: linear-gradient(269.98deg, #eecb12 27.19%, #fff0 56.18%), #fa7b05;
}

.IvaCSa_frame p {
  text-align: left;
  font-size: small;
}

.IvaCSa_frame .IvaCSa_logoTag {
  object-fit: contain;
  width: 140px;
  height: 42px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%);
}

.IvaCSa_card {
  white-space: pre-line;
  background-color: #745491;
}

.Fbh54a_user_container {
  justify-content: center;
  height: calc(100vh - 155px);
  padding: 60px 100px;
  display: flex;
}

.Fbh54a_user_container_left {
  border: 1px solid #dee2e6;
  border-radius: 8px;
  justify-content: center;
  width: 200px;
  height: 300px;
  margin-right: 80px;
  padding: 20px;
  display: flex;
}

.Fbh54a_user_container_avatar {
  width: 40px;
  height: 40px;
  margin-right: 15px;
}

.Fbh54a_table_container {
  max-height: 500px;
  margin-top: 50px;
  overflow-y: auto;
}

.Fbh54a_table_td {
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 300px;
  overflow: hidden;
}

.Fbh54a_btn_container {
  width: 100%;
  padding: 20px 0 0 45px;
  position: absolute;
  bottom: 20px;
}

.Fbh54a_activity_container {
  margin-left: 40px;
}

.Fbh54a_activity_panel {
  display: inline-flex !important;
}

.Fbh54a_user_card {
  width: 255px;
  height: 350px;
}

.Fbh54a_activity_item {
  margin: 0 20px 20px 0;
}

.Fbh54a_avatar {
  border-radius: 50%;
  width: 120px;
  height: 120px;
}
